.join {
  display: flex;
  background: radial-gradient(50% 50% at 50% 50%, #403a5f 0%, #211e2e 100%);
  justify-content: space-between;
  align-items: center;
  max-width: 1275px;
  min-height: 1008px;
  flex-direction: column;
  margin: 0 auto;
  border-radius: 40px;
  box-shadow: 0px 4px 4px 0px #00000040;
  margin-bottom: 122px;
  margin-top: 112px;

  .join__h2 {
    text-align: center;
    max-width: 238px;
    height: 44px;
    font-size: 36px;
    font-weight: 700;

  }

  hr{
      width: 414px;
      height: 0;
      border-color: linear-gradient(
        270deg,
        #343045 0%,
        #c0b7e8 34.9%,
        #8176af 68.75%,
        #343045 100%
      );
      border: 1px solid;
      border-image-source: linear-gradient(
        270deg,
        #343045 0%,
        #c0b7e8 34.9%,
        #8176af 68.75%,
        #343045 100%
      );
      border-image-slice: 1;
      
    
  }

  .join__text {
    text-align: center;
    max-width: 541px;
    height: 44px;
    font-size: 36px;
    font-weight: 200;
    line-height: 43.88px;
    margin-top: 20px;
  }

  .join__input {
    width: 524px;
    height: 72px;
    background-color: transparent;
    border: 2px solid #ffffff;
    border-radius: 40px;
    transition: 0.2s;
    &:focus {
        border: 4px solid #8176AF;
        outline: #211e2e;
    }
  }
  .join__input__subject {
    width: 1058px;
    height: 72px;
    background-color: transparent;
    border: 2px solid #ffffff;
    border-radius: 40px;
    transition: 0.2s;
    &:focus {
        border: 4px solid  #8176AF;
        outline: #211e2e;
    }
  }
  .join__input__something {
    width: 1058px;
    height: 210px;
    background-color: transparent;
    border: 2px solid #ffffff;
    border-radius: 40px;
    transition: 0.2s;
    &:focus {
        border: 4px solid  #8176AF;
        outline: #211e2e;
    }
  }
  .join__button {
    border: 0;
    width: 214px;
    height: 48px;
    border-radius: 40px;
    background: linear-gradient(90deg, #8176af 0%, #c0b7e8 100%);
    color: #000;
    transition: all ease-in-out 0.3s;
    margin-bottom: 20px;
    -moz-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    &:hover{
        color:  #fff;
        text-shadow:0px 0px 5px  #fff;
        -moz-transition: all 0.3s ease-in;
        -o-transition: all 0.3s ease-in;
        -webkit-transition: all 0.3s ease-in;
        transition: all 0.3s ease-in;
        transform: scale(1.1);
    }
  }
}

@media screen and (max-width: 1051px) {
  .join__input {
    max-width: 424px;
    &::placeholder {
      padding: 21px;
    }
  }

  .join__input__subject {
    max-width: 864px;
    &::placeholder {
      padding: 21px;
    }
  }
  .join__input__something {
    max-width: 864px;
    &::placeholder {
      padding: 21px;
    }
  }
}

@media screen and (max-width: 425px) {
  .join {
    margin: unset;
    padding: unset;
  }
  .join__div__input1,
  .join__div__input2 {
    width: 300px;
    height: 150px;
    display: grid;
    grid-template: 1fr 1fr;
    grid-gap: 15px;
  }

  .join__input {
    max-width: 300px;
    height: 53px;
    &::placeholder {
      padding: 29px;
    }
  }

  .join__input__subject {
    max-width: 300px;
    height: 53px;
    &::placeholder {
      padding: 29px;
    }
  }
  .join__input__something {
    max-width: 300px;
    height: 53px;
    &::placeholder {
      padding: 29px;
    }
  }
}

@media screen and (max-width: 425px) {
  hr {

      max-width: 400px;
  }
}

@media screen and (max-width: 375px) {
  hr{

      max-width: 350px;
    
  }
}

@media screen and (max-width: 320px) {
  hr{
   
      max-width: 300px;
    
  }
}
